import React, { useState, useEffect } from "react"
import { Link } from "gatsby"
import { useSelector, shallowEqual, useDispatch } from "react-redux"

import auth from "../../../Auth/utils/auth"

const UserMenu = () => {
  const [agentImageState, setAgentImageState] = useState("")

  const userState = useSelector(state => state.authReducer.user, shallowEqual)

  const images = require.context("../../../assets/demo-imgs", true)

  useEffect(() => {
    if (userState.photo) {
      setAgentImageState(images("./" + userState.photo))
    }
  }, [userState])

  if (Object.keys(userState).length > 0) {
    return (
      <ul className="attributes">
        <li className="login-attri">
          <div className="btn-group account-drop">
            <Link to="/profile" className="btn btn-order-by-filt">
              <img
                src={
                  agentImageState === ""
                    ? "https://via.placeholder.com/400x400"
                    : agentImageState
                }
                className="avater-img"
                alt=""
              />
              Hi, {userState.name}
            </Link>
          </div>
        </li>
      </ul>
    )
  } else {
    return null
  }
}

export default UserMenu
